import { error, json } from "@sveltejs/kit";
import { eq, desc, and } from "drizzle-orm";
import { c as createDb, s as savedFontPairings } from "../../../../../chunks/index3.js";
import { g as generateId } from "../../../../../chunks/stripe.js";
const GET = async ({ locals, platform }) => {
  if (!locals.user) {
    error(401, "Not authenticated");
  }
  if (!locals.user.isPremium) {
    error(403, "Premium subscription required");
  }
  const databaseUrl = platform?.env?.DATABASE_URL;
  if (!databaseUrl) {
    error(500, "Database not configured");
  }
  const db = createDb(databaseUrl);
  const pairings = await db.select().from(savedFontPairings).where(eq(savedFontPairings.userId, locals.user.id)).orderBy(desc(savedFontPairings.createdAt));
  return json({ pairings });
};
const POST = async ({ locals, platform, request }) => {
  if (!locals.user) {
    error(401, "Not authenticated");
  }
  if (!locals.user.isPremium) {
    error(403, "Premium subscription required");
  }
  const databaseUrl = platform?.env?.DATABASE_URL;
  if (!databaseUrl) {
    error(500, "Database not configured");
  }
  const db = createDb(databaseUrl);
  const body = await request.json();
  const { pairingName, notes } = body;
  if (!pairingName || typeof pairingName !== "string") {
    error(400, "Pairing name is required");
  }
  const [existing] = await db.select().from(savedFontPairings).where(
    and(
      eq(savedFontPairings.userId, locals.user.id),
      eq(savedFontPairings.pairingName, pairingName)
    )
  ).limit(1);
  if (existing) {
    error(400, "Font pairing already saved");
  }
  const [saved] = await db.insert(savedFontPairings).values({
    id: generateId(),
    userId: locals.user.id,
    pairingName,
    notes: notes || null
  }).returning();
  return json({ pairing: saved }, { status: 201 });
};
const DELETE = async ({ locals, platform, request }) => {
  if (!locals.user) {
    error(401, "Not authenticated");
  }
  if (!locals.user.isPremium) {
    error(403, "Premium subscription required");
  }
  const databaseUrl = platform?.env?.DATABASE_URL;
  if (!databaseUrl) {
    error(500, "Database not configured");
  }
  const db = createDb(databaseUrl);
  const body = await request.json();
  const { pairingName } = body;
  if (!pairingName || typeof pairingName !== "string") {
    error(400, "Pairing name is required");
  }
  await db.delete(savedFontPairings).where(
    and(
      eq(savedFontPairings.userId, locals.user.id),
      eq(savedFontPairings.pairingName, pairingName)
    )
  );
  return json({ success: true });
};
export {
  DELETE,
  GET,
  POST
};
