import { redirect, fail } from "@sveltejs/kit";
import { eq } from "drizzle-orm";
import { c as createLucia } from "../../../chunks/auth.js";
import { c as createDb, u as users } from "../../../chunks/index3.js";
import { v as verifyPassword } from "../../../chunks/password.js";
const load = async ({ locals }) => {
  if (locals.user && locals.session) {
    redirect(302, "/");
  }
  return {};
};
const actions = {
  default: async ({ request, cookies, platform }) => {
    const formData = await request.formData();
    const email = formData.get("email");
    const password = formData.get("password");
    if (typeof email !== "string" || !email.includes("@")) {
      return fail(400, { error: "Invalid email address" });
    }
    if (typeof password !== "string" || password.length < 1) {
      return fail(400, { error: "Password is required" });
    }
    const databaseUrl = platform?.env?.DATABASE_URL;
    if (!databaseUrl) {
      return fail(500, { error: "Database not configured" });
    }
    const db = createDb(databaseUrl);
    const lucia = createLucia(db);
    const [existingUser] = await db.select().from(users).where(eq(users.email, email.toLowerCase())).limit(1);
    if (!existingUser) {
      return fail(400, { error: "Invalid email or password" });
    }
    const validPassword = await verifyPassword(existingUser.passwordHash, password);
    if (!validPassword) {
      return fail(400, { error: "Invalid email or password" });
    }
    const session = await lucia.createSession(existingUser.id, {});
    const sessionCookie = lucia.createSessionCookie(session.id);
    cookies.set(sessionCookie.name, sessionCookie.value, {
      path: "/",
      ...sessionCookie.attributes
    });
    redirect(302, "/");
  }
};
export {
  actions,
  load
};
