import { redirect } from "@sveltejs/kit";
import { eq, desc } from "drizzle-orm";
import { c as createDb, f as customPalettes } from "../../../chunks/index3.js";
const load = async ({ locals, platform }) => {
  if (!locals.user) {
    redirect(302, "/login");
  }
  if (!locals.user.isPremium) {
    redirect(302, "/pricing");
  }
  const databaseUrl = platform?.env?.DATABASE_URL;
  let userPalettes = [];
  if (databaseUrl) {
    const db = createDb(databaseUrl);
    userPalettes = await db.select({
      id: customPalettes.id,
      name: customPalettes.name,
      light: customPalettes.light,
      dark: customPalettes.dark
    }).from(customPalettes).where(eq(customPalettes.userId, locals.user.id)).orderBy(desc(customPalettes.createdAt));
  }
  return { userPalettes };
};
export {
  load
};
