import { redirect, error } from "@sveltejs/kit";
import { eq } from "drizzle-orm";
import { c as createDb, u as users } from "../../../../chunks/index3.js";
const load = async ({ params, locals, platform }) => {
  if (!locals.user) {
    redirect(302, "/login");
  }
  const isOwnProfile = locals.user.id === params.id;
  const isAdmin = locals.user.isAdmin;
  if (!isOwnProfile && !isAdmin) {
    error(403, "You can only view your own profile");
  }
  if (!platform?.env?.DATABASE_URL) {
    error(500, "Database not configured");
  }
  const db = createDb(platform.env.DATABASE_URL);
  const [profileUser] = await db.select({
    id: users.id,
    email: users.email,
    name: users.name,
    isAdmin: users.isAdmin,
    createdAt: users.createdAt
  }).from(users).where(eq(users.id, params.id)).limit(1);
  if (!profileUser) {
    error(404, "User not found");
  }
  return { profileUser };
};
export {
  load
};
